import mpvueCityPicker from '../../common/mpvue-citypicker/mpvueCityPicker.vue';
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			userpic: '../../static/demo/topicpic/9.jpeg',
			nickname: '哈哈',
			sex: '不限',
			sexList: ['不限', '男', '女'],
			qgList: ['秘密', '未婚', '已婚'],
			jobList: ['秘密', 'IT', '教师', '工人'],
			qg: '已婚',
			job: 'IT',
			birthday: '1990-01-01',
			themeColor: '#007AFF',
			cityPickerValueDefault: [0, 0, 1],
			cityAddr: '广东-广州'
		};
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
		}
	},
	methods: {
		changeImg() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'],
				success: res => {
					this.userpic = res.tempFilePaths[0];
					console.log(JSON.stringify(res));
				}
			});
		},
		//修改生日
		bindDateChange(e) {
			this.birthday = e.target.value;
		},
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show();
		},
		onConfirm(e) {
			this.cityAddr = e.label;
			this.cityPickerValueDefault = e.value;
			// this.pickerText = JSON.stringify(e)
			console.log(JSON.stringify(e));
		},
		onCancel(e) {
			console.log(e);
		},
		changeList(val) {
			const _this = this;
			if (val == 'sex') {
				uni.showActionSheet({
					itemList: _this.sexList,
					success: res => {
						_this.sex = _this.sexList[res.tapIndex];
						// console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					}
				});
			} else if (val == 'qg') {
				uni.showActionSheet({
					itemList: _this.qgList,
					success: res => {
						_this.qg = _this.qgList[res.tapIndex];
						// console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					}
				});
			} else if (val == 'job') {
				uni.showActionSheet({
					itemList: _this.jobList,
					success: res => {
						_this.job = _this.jobList[res.tapIndex];
						// console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					}
				});
			}
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	}
};
